import React from 'react';


const Card = ({id , name , email}) =>{
	return (
		<div className='tc bg-light-green dib br3 pa3 ma2 grow'>
           <img src={"https://robohash.org/${id}?100*100"} alt="robot"/>
           <div>
           <h2>{name}</h2>
           <p>{email}</p>
           </div>
         </div>







		);




}


export default Card;


