import React from 'react';

const SearchBox = ({searchfield , searchChange}) =>{
	return (
			<div className='pa2'>
			<input  className='p2'
			type='search'
			placeholder='SearchBox'
			onChange={searchChange }
			/>
			
			</div>


		);
}


export default SearchBox;


